<template>
    <div>
        <el-card class="box-card">
            <span>保修列表</span>
        </el-card>
        <el-row>
            <el-col :span="24">
                <el-form ref="form" :model="query" label-width="96px">
                    <div class="op">
                        <el-form-item label="投诉标题">
                            <el-input @change="serate" clearable @keyup.native.enter="serate" v-model="query.key" placeholder="投诉标题"></el-input>
                        </el-form-item>
                        <el-form-item label="地址">
                            <el-input @change="serate" clearable @keyup.native.enter="serate" v-model="query.address" placeholder="地址"></el-input>
                        </el-form-item>
                        <el-form-item label="电话">
                            <el-input @change="serate" clearable @keyup.native.enter="serate" v-model="query.tel" placeholder="电话"></el-input>
                        </el-form-item>
                        <el-form-item label="按时间查询">
                            <el-date-picker
                            v-model="query.begindate"
                            type="daterange"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期">
                            </el-date-picker>
                        </el-form-item>
                        <el-form-item label="投诉状态">
                            <el-select @change="serate" clearable @keyup.native.enter="serate" v-model="query.state" placeholder="请选择">
                                <el-option v-for="(imet,index) in list" :key="index" :label="imet.name" :value="imet.id"></el-option>
                            </el-select>
                        </el-form-item>
                    </div>
                    <div class="ol">
                        <el-form-item label="投诉类型">
                            <el-select @change="serate" clearable @keyup.native.enter="serate" v-model="query.type" placeholder="请选择">
                                <el-option v-for="(imet,index) in typename" :key="index" :label="imet.name" :value="imet.id"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="居民">
                            <el-input @change="serate" clearable @keyup.native.enter="serate" v-model="query.userid" placeholder="请输入关键字"></el-input>
                        </el-form-item>
                          <el-form-item>
                            <el-button type="primary" @click="serate">查询</el-button>
                        </el-form-item>
                    </div>
                    
                </el-form>
            </el-col>
        </el-row>
        <el-row>
            <el-button type="danger" @click="del">批量删除</el-button>
        </el-row>
        <!-- 表格 -->
            <Table
            :loading="loading"
            :tableData="tableData"
            :tableCouml="tableCouml"
            @idshow="idshow">

            <template #title="data">
                <a @click="deladd(data.data)" href="javascript:void(0);">{{data.data.typename}}</a>
            </template>

            <template #imgage="data">
            <p @click="ol(data.data.images)">
                <el-image
                style="width: 50px; height: 50px;"
                :src="img+url"
                fit="fill"></el-image>
                <el-image
                style="width: 50px; height: 50px;margin-left: 10px;"
                :src="img+url"
                fit="fill"></el-image>
            </p>
          </template>

            <template #option="data">
                <el-button size="mini" type="success" @click="mod(data.data)">修改</el-button>
                <el-button size="mini" type="danger" @click="handleDelete(data.data)">删除</el-button>
            </template>
        </Table>
        <!-- 分页 -->
            <Paging :query="query" :total="total" @paging="paging"></Paging>
        <!-- 详情展示 -->
        <Reairsadd :isNewsadd="isNewsadd" :Newsdata="Newsdata" @change="change"></Reairsadd>
    </div>
</template>

<script>
import Table from "@/components/commts/Table.vue";
import Paging from "@/components/commts/Paging.vue";
import Reairsadd from "@/components/repairs/repairsadd.vue"

export default {
    data(){
        return{
            img:'http://estate.eshareedu.cn/estate/upload/',
            loading: true,
            query:{
                page:1,
                psize:5
            },
            tableData: [],
            tableCouml: [
                    { prop: "id", label: "ID",},
                    { prop: "typename", label: "故障类型", isshow: true, showname: "title"},
                    { prop: "address", label: "维修地址",},
                    { prop: "images", label: "故障图片", isshow:true,showname: "imgage",},
                    { prop: "content", label: "故障描述", },
                    { prop: "statename", label: "维修状态",},
                    { prop: "username", label: "业主名称",},
                    { prop: "addtime", label: "维修日期",},
                    { label: "操作", isshow: true, showname: "option" },
                ],
                total:0,
                ids:[],
                Newsdata:{},
                isNewsadd:false,
                typename:[],
                list:[],
                url:''
        }
    },
    components:{
        Table,
        Reairsadd,
        Paging
    },
    created(){
        this.getimet()
        this.gettypename()
        this.getstatename()
    },
    methods:{
        ol(val){
            val.forEach(imet => {
                    // console.log(imet.url)
                    this.url = imet.url
            });
            console.log(this.url)
        },
        // 查询
        serate(){
            this.getimet()
        },
        // 投诉状态
        async getstatename(){
            let res = await this.http.get('/repairstate/list',this.query)
            // console.log(res)
            if(res.errCode == 10000){
                this.list = res.data.list
            }
        },
        // 问题类型
        async gettypename(){
            let res = await this.http.get('/repairstype/list',this.query)
            // console.log(res)
            if(res.errCode == 10000){
                this.typename = res.data.list
            }
        },
        // 刷新页面
        paging() {
            this.getimet();
        },
        // 详情
        deladd(val){
            // console.log(val)
            this.isNewsadd = true
            this.Newsdata = val
        },
        change(){
            this.isNewsadd = false
        },
        // 批量删除
        del(){
            // console.log(this.ids)
        this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning",
        })
            .then(async () => {
            let res = await this.http.post("/repairs/deleteall", {ids: this.ids,});
            if (res.errCode == 10000) {
                this.$message.success("删除成功");
                this.getimet();
            }
            })
            .catch(() => {
            this.$message({
                type: "info",
                message: "已取消删除",
            });
            });
        },
        idshow(val){
            this.ids.push(val)
            // console.log(val)
        },
        // 删除
        handleDelete(val){
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(async () => {
              let res = await this.http.get('/questions/delete',{id:val.id})
              if(res.errCode == '10000'){
                  this.$message.success('删除成功')
                  this.getimet()
              }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        },
        // 列表
        async getimet(){
            let res = await this.http.get('/repairs/list',this.query)
            // console.log(res)
            if(res.errCode == '10000'){
            this.tableData = res.data.list
            this.total = res.data.counts
            this.loading = false
            }
      },

    }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/admin.css';
.el-form{
    display: block;
    .op{
        display: flex;
        }
.ol{
    display: flex;
}
}
a{
    text-decoration: underline;
}
/deep/.el-date-editor .el-range-input{
    background-color: transparent !important;
}
</style>